<template>
    <div>
        <div class="pricing-header px-3 py-3 pt-md-5 pb-md-4 mx-auto text-center">
            <h1 class="display-4">Cadastro de Gasto</h1>
        </div>

        <div v-show="mensagem" class="alert alert-danger" role="alert">
            {{mensagem}}
        </div>

        <form @submit.prevent="grava()">
            <div class="form-group row">
                <label for="staticNome" class="col-sm-2 col-form-label">Nome</label>
                <div class="col-sm-6">
                    <input name="nome" class="form-control" id="staticNome" v-model="gasto.nome"/>
                    <span class="text-danger" v-show="errors.has('nome')">{{ errors.first('nome') }}</span>
                </div>
            </div>
            <div class="form-group row">
                <label for="staticDescicao" class="col-sm-2 col-form-label">Descrio</label>
                <div class="col-sm-8">
                    <textarea name="descricao" class="form-control" id="staticDescicao"
                              v-model="gasto.descricao"></textarea>
                    <span class="text-danger" v-show="errors.has('descricao')">{{ errors.first('descricao') }}</span>
                </div>
            </div>
            <div class="form-group row">
                <label for="staticValor" class="col-sm-2 col-form-label">Valor</label>
                <div class="col-sm-2">
                    <money v-model="gasto.valor" v-bind="money" class="form-control" name="valor"
                           id="staticValor"></money>
                    <span class="text-danger" v-show="errors.has('valor')">{{ errors.first('valor') }}</span>
                </div>
            </div>
            <div class="form-group row">
                <label for="staticTag" class="col-sm-2 col-form-label">Tag</label>
                <div class="col-sm-2">
                    <input name="tag" class="form-control" id="staticTag" type="text" v-model="gasto.tag"/>
                    <span class="text-danger" v-show="errors.has('tag')">{{ errors.first('tag') }}</span>
                </div>
            </div>
            <div class="form-group row">
                <div class="col-sm-10 offset-2">
                    <button type="submit" class="btn btn-success">Salvar</button>
                    <router-link :to="{name:'home'}" class="btn btn-primary">Voltar</router-link>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
    import Gasto from '../../domain/gasto/Gasto';
    import GastoService from '../../domain/gasto/GastoService';
    import {Money} from 'v-money'

    export default {

        components: {Money},
        data() {
            return {
                gasto: new Gasto(),
                mensagem: '',
                money: {
                    decimal: ',',
                    thousands: '.',
                    prefix: 'R$ ',
                    suffix: '',
                    precision: 2,
                    masked: false
                }
            }
        },
        methods: {
            grava() {

                this.service.cadastra(this.gasto).then(() => {

                        this.$router.push({name: 'home', params: {mensagem: 'Cadastro realizado com sucesso'}});

                    },
                    err => {

                        this.errors.clear();

                        if (err.body.hasOwnProperty('errors')) {

                            let errorFields = Object.keys(err.body.errors);

                            errorFields.map(field => {
                                let errorString = err.body.errors[field].join(', ');
                                this.errors.add(field, errorString);
                            });
                        }

                        this.mensagem = err.body.message;

                        console.log(err);
                    }
                );
            }
        },
        created() {

            this.service = new GastoService(this.$resource);
        }
    }
</script>

<style scoped>

</style>


