<template>
    <div>
        <div class="pricing-header px-3 py-3 pt-md-5 pb-md-4 mx-auto text-center">
            <h1 class="display-4">Lista de Gastos</h1>
        </div>

        <div v-show="mensagem" class="alert alert-success" role="alert">
            {{mensagem}}
        </div>

        <table class="table table-houver">
            <thead>
            <tr>
                <th>Nome da Pessoa</th>
                <th>Valor</th>
                <th>Data/Hora</th>
                <th>Opo</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="gasto of gastos">
                <td>{{gasto.nome}}</td>
                <td>{{(gasto.valor).toLocaleString('pt-BR',{ style: 'currency', currency: 'BRL' })}}</td>
                <td>{{ new Date(gasto.created_at.date).toLocaleString()}}</td>
                <td>
                    <router-link :to="{ name: 'detalhe', params:{id: gasto.id}}" class="btn btn-primary btn-sm">
                        Detalhe
                    </router-link>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>

    import GastoService from '../../domain/gasto/GastoService';

    export default {
        data() {
            return {
                gastos: [],
                mensagem: ''
            }
        },
        created() {

            this.service = new GastoService(this.$resource);

            this.service.lista().then(gastos => this.gastos = gastos.data, err => this.mensagem = err.message);

            if (this.$route.params.mensagem) {
                this.mensagem = this.$route.params.mensagem;
            }
        }
    }
</script>

<style scoped>

</style>


