<template>
    <div>
        <div class="pricing-header px-3 py-3 pt-md-5 pb-md-4 mx-auto text-center">
            <h1 class="display-4">Detalhe do Gasto</h1>
        </div>
        <form>
            <div class="form-group row">
                <label for="staticNome" class="col-sm-2 col-form-label">Nome</label>
                <div class="col-sm-10">
                    <p class="form-control-plaintext" id="staticNome">{{gasto.nome}}</p>
                </div>
            </div>
            <div class="form-group row">
                <label for="staticDescicao" class="col-sm-2 col-form-label">Descrio</label>
                <div class="col-sm-10">
                    <p class="form-control-plaintext" id="staticDescicao">{{ gasto.descricao}}</p>
                </div>
            </div>
            <div class="form-group row">
                <label for="staticValor" class="col-sm-2 col-form-label">Valor</label>
                <div class="col-sm-10">
                    <p class="form-control-plaintext" id="staticValor">{{(gasto.valor).toLocaleString('pt-BR',{ style:
                        'currency', currency: 'BRL' })}}</p>
                </div>
            </div>
            <div class="form-group row">
                <label for="staticTag" class="col-sm-2 col-form-label">Tag</label>
                <div class="col-sm-10">
                    <p class="form-control-plaintext" id="staticTag">{{gasto.tag}}</p>
                </div>
            </div>
            <div class="form-group row">
                <label for="staticData" class="col-sm-2 col-form-label">Data/Hora</label>
                <div class="col-sm-10">
                    <p class="form-control-plaintext" id="staticData">{{new
                        Date(gasto.created_at.date).toLocaleString()}}</p>
                </div>
            </div>
            <div class="form-group row">
                <div class="col-sm-10 offset-2">
                    <router-link :to="{name:'home'}" class="btn btn-primary">Voltar</router-link>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
    import Gasto from "../../domain/gasto/Gasto";
    import GastoService from "../../domain/gasto/GastoService";

    export default {

        data() {
            return {
                gasto: new Gasto(),
                id: this.$route.params.id
            }
        },
        created() {
            this.service = new GastoService(this.$resource);

            if (this.id) {
                this.service.busca(this.id).then(gasto => this.gasto = gasto.data);
            }
        }
    }
</script>

<style scoped>

</style>


