export default class GastoService {

    constructor(resource) {

        this._resource = resource('api/gastos{/id}');
    }

    cadastra(gasto) {

        return this._resource.save(gasto);
    }

    lista() {

        return this._resource.query().then(res => res.json(), err => {
            console.log(err);
            throw new Error('No foi possvel obter as fotos. Tenta mais tarde');
        });
    }

    busca(id) {

        return this._resource.get({id}).then(res => res.json());
    }
}


