<template>
    <div class="d-flex flex-column flex-md-row align-items-center p-3 px-md-4 mb-3 bg-white border-bottom shadow-sm">
        <h5 class="my-0 mr-md-auto font-weight-normal">Controle de Gastos</h5>
        <nav class="my-2 my-md-0 mr-md-3">
            <router-link v-for="rota of rotas" :to="rota.path ? rota.path : '/'" class="p-2 text-dark">
                {{ rota.titulo }}
            </router-link>
        </nav>
    </div>
</template>

<script>
    export default {
        props: {
            rotas: {
                type: Array,
                required: true
            }
        }
    }
</script>

<style scoped>

</style>


