export default class Gasto {

    constructor(nome = '', descricao = '', valor = '', tag = '', created_at = {date: ''}) {

        this.nome = nome;
        this.descricao = descricao;
        this.valor = valor;
        this.tag = tag;
        this.created_at = created_at;
    }
}


